<template>
  <van-cell class="comment-item">
    <!-- 头像 -->
    <van-image
      class="comment_avatar"
      slot="icon"
      round
      fit="cover"
      src="https://img01.yzcdn.cn/vant/cat.jpeg"
    />
    <!-- 上部分 -->
    <div slot="title" class="author_name">
      <div class="title">
        <div class="author">{{ comment.aut_name }}</div>
        <div class="like_wrap" @click="onLike">
          <van-icon
            class="like_icon"
            :name="comment.is_liking ? `good-job` : `good-job-o`"
            :color="comment.is_liking ? `#ff69b4` : `#777`"
          />
          <span class="like_count">{{ comment.like_count }}</span>
        </div>
      </div>
      <!-- 正文 -->
      <div class="content">
        {{ comment.content }}
      </div>

      <!-- 下部分 -->
      <div class="btm">
        <span class="pubdate">{{
          comment.pubdate | commentDate(`MM-DD HH:mm`)
        }}</span>
        <van-button round size="mini"
         @click="$emit(`onReplay`, comment)">
          {{ comment.reply_count }} 回复
        </van-button>
      </div>
    </div>
  </van-cell>
</template>

<script>
import { deleteCommentLike, addCommentLike } from '@/api/article.js';

export default {
  name: 'CommentItem',
  props: ['comment'],
  data() {
    return {};
  },
  methods: {
    async onLike() {
      try {
        if (this.comment.is_liking) {
          await deleteCommentLike(this.comment.com_id);
          this.comment.like_count -= 1;
        } else {
          await addCommentLike(this.comment.com_id);
          this.comment.like_count += 1;
        }
        this.comment.is_liking = !this.comment.is_liking;
      } catch (e) {
        console.log(e);
      }
    },
  },
  created() {},
};
</script>

<style lang="less" scoped>
.comment-item {
  .comment_avatar {
    width: 36px;
    height: 36px;
    margin-right: 13px;
  }
  .title {
    display: flex;
    justify-content: space-between;
    .author {
      font-size: 14px;
      color: #406599;
    }
    .like_wrap {
      display: flex;
      align-items: center;
      .like_icon {
        font-size: 16px;
      }
    }
  }
  .content {
    padding: 6px 0;
    font-size: 14px;
    color: #222;
  }
  .btm {
    display: flex;
    .pubdate {
      font-size: 10px;
      margin-right: 10px;
    }
  }
}
</style>
